.heart {
    position: absolute;
    &:after {
        top: 0%; }
    &.one {
        top: 34vh;
        left: 25%;
        z-index: 99;
        @include MQ(XS) {
            left: 13%; }
        @include MQ(S) {
            left: 5%; }
        @include MQ(M) {
            left: 5%; }
        &:after {
            background-image: url('../images/theme/heart_one.png');
            @include w-size(138);
            @include h-size(113);
            @include rotate(22);
            @include MQ(XS) {
                @include w-size(250);
                @include h-size(205); } } }
    &.two {
        bottom: 39%;
        right: 28%;
        &:after {
            @include afterimg;
            background-image: url('../images/theme/heart_two.png');
            @include w-size(250);
            @include h-size(205);
            @include rotate(22); } } }

