
.envelope {
    position: absolute;
    &:after {
        @include afterimg; }
    &.one {
        display: none;
        @include MQ(XS) {
            display: block;
            top: 5vh;
            right: 25%; }
        @include MQ(S) {
            top: -10vh;
            right: 10%; }
        @include MQ(M) {
            right: 0%; }
        @media (min-width: 1280px) {
            right: 15%; }
        @include MQ(L) {
            right: 35%; }
        &:after {
            background-image: url('../images/site/envelope.png');
            @include w-size(240);
            @include h-size(461);
            @include MQ(S) {
                @include w-size(338);
                @include h-size(650); } } }
    &.two {
        top: 15vh;
        left: 0;
        @include MQ(XS) {
            top: 25vh;
            left: 07%; }
        @include MQ(S) {
            top: 20vh;
            left: -7%; }
        @include MQ(M) {
            left: 20%; }
        @include MQ(L) {
            left: 20%; }
        &:after {
            background-image: url('../images/site/envelope_burlap.png');
            @include w-size(322);
            @include h-size(410);
            @include MQ(XS) {
                @include w-size(378);
                @include h-size(480); } } }
    &.open {
        top: 2%;
        right: 24%;
        &:after {
            background-image: url('../images/site/envelope_open.png');
            width: 486px;
            height: 650px; } } }
